.show-header{
    position: relative;

    .bg-image{
        background: url('../../images/shows/cinderella_cover.jpg') no-repeat center;
        height: 30vw;
        display: block;
        background-size: cover;
    }

    .show-navigation{
        position: absolute;
        top: 2em;
        width: 100%;

        .prev{
            position: absolute;
            left: 0;
        }

        .next{
            position: absolute;
            right: 0;
        }
    }

    .content{
        padding: 4em 4em 0;
        position: relative;

        
        .dash{
            margin-top: 2.5em;
        }
    }

}

.cast-list dl{
        margin-top: .5em;
}

.show-content{
    padding: 0 4em 4em;

        h4 + p{
            margin-top: 1.5em;
        }
}

#show{ .grid{ margin-bottom: 0; }}

@media screen and (min-width: 800px){

    .show-header{

        .button{
            position: absolute;
            top: 3.25em;
            left: 1rem;
        }
    }

    .cast-list{
        dl{
            width: 50%;
            float: left;
            
        }
    }

}
